body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,h2,h3,h4,h5,p,label{
  color: #45460c;
}

button span{
  color: #fff !important
}

.nav-item.active a{
  color: #ff7f00 !important;
  border-bottom: 3px solid #8f931f;
}

.parallax_physio {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url("https://www.fitback.co.uk/wp-content/uploads/2015/02/fitback_banner_2-1900x700.jpg");
  min-height: calc(50vh) !important;

}